/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-vayu-blocks {
	border: 1px dotted #f00;
} 

.components-panel__body.th-container-panel label,
.components-panel__body.th-container-panel span{
    text-transform: capitalize!important;
    font-size: 13px!important;
    line-height: 1.4em!important;
    font-weight:normal!important;
}
	
.block-list-appender.wp-block,
.th-blocks-container-selector{
width:100%;
}
.wp-block-vayu-blocks-advance-container:not(.is-selected):before  {
    // border: 1px dashed;
    border-radius: 2px;
    bottom: 0;
    content: "";
    left: 0;
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
}
.block-editor-block-list__layout .block-editor-block-list__block:not([contenteditable=true]):hover:after{
    bottom: 0;
    content: "";
    left: 0;
    outline-color: var(--wp-admin-theme-color);
    outline-offset: calc((-1* var(--wp-admin-border-width-focus)) / var(--wp-block-editor-iframe-zoom-out-scale, 1));
    outline-style: solid;
    outline-width: calc(var(--wp-admin-border-width-focus) / var(--wp-block-editor-iframe-zoom-out-scale, 1));
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
}


.th-blocks-container-selector__icon{
margin: 0 auto;
}

.block-editor-block-list__block.is-selected .th-inside-container > .block-list-appender:only-child:after {
    border:0px dashed;
}
.is-selected>.th-inside-container>.block-list-appender{
width:auto;
}
.components-button.th-blocks-container-selector {
	padding: 12px;
	height:auto;
	}
	
	.th-inside-container .block-list-appender.wp-block {
		padding:0px;
	}
	
	.is-selected > .th-inside-container > .block-list-appender > .block-editor-inserter > button{
		padding: 12px;
		height:auto;
		border:0;
		box-shadow:none;
		background:none;
		color:#111;
		}
	
	.th-root-block-container > .is-selected > .th-inside-container > .block-list-appender > .block-editor-inserter > button{
			padding: 0; 
			color: #fff; 
			background: #000;
	}

	// .editor-styles-wrapper .th-root-block-container{
    //  display:flex;
	//  justify-content: center;
	// }

.wp-block-vayu-blocks-advance-container{
	--background:none;
	--background-hover: var(--background);
	--transition:0.2s;
	--boxShadow:var(--boxShadow);
	--boxShadow-hvr:var(--boxShadow);
	--background-attachment-hvr:var(--background-attachment);
	--background-position-hvr:var(--background-position);
	--background-repeat-hvr:var(--background-repeat);
	--background-size-hvr:var(--background-size);
	--border-top-hvr:var(--border-top);
	--border-bottom-hvr:var(--border-bottom);
	--border-right-hvr:var(--border-right);
	--border-left-hvr:var(--border-left);
	--border-TopRight-Radius-hvr:var(--border-TopRight-Radius);
	--border-TopLeft-Radius-hvr:var(--border-TopLeft-Radius);
	--border-BottomRight-Radius-hvr:var(--border-BottomRight-Radius);
	--border-BottomLeft-Radius-hvr:var(--border-BottomLeft-Radius);
	--border-style-hvr:var(--border-style);
	--border-color-hvr:var(--border-color);
	background: var(--background);
	background-repeat: var(--background-repeat);
	background-size: var(--background-size);
	background-position: var(--background-position);
	background-attachment: var(--background-attachment);
	padding-top:var(--padding-top);
	padding-right:var(--padding-right);
	padding-bottom:var(--padding-bottom);
	padding-left:var(--padding-left);
	margin-top:var(--margin-top);
	margin-right:var(--margin-right);
	margin-bottom:var(--margin-bottom);
	margin-left:var(--margin-left);
	border-style:var(--border-style);
	
	border-color:var(--border-color);
	border-top-width:var(--border-top);
	border-bottom-width:var(--border-bottom);
	border-right-width:var(--border-right);
	border-left-width:var(--border-left);
	border-top-right-radius:var(--border-TopRight-Radius);
	border-top-left-radius:var(--border-TopLeft-Radius);
	border-bottom-right-radius:var(--border-BottomRight-Radius);
	border-bottom-left-radius:var(--border-BottomLeft-Radius);
	box-shadow:var(--boxShadow);
	gap:var(--gap);
	
	&:hover {
		background: var(--background-hover);
		background-repeat: var(--background-repeat-hvr);
		background-size: var(--background-size-hvr);
		background-position: var(--background-position-hvr);
		background-attachment: var(--background-attachment-hvr);
		border-style:var(--border-style-hvr);
	    border-color:var(--border-color-hvr);
		box-shadow:var(--boxShadow-hvr);
		border-top-width:var(--border-top-hvr);
		border-bottom-width:var(--border-bottom-hvr);
		border-right-width:var(--border-right-hvr);
		border-left-width:var(--border-left-hvr);
		border-top-right-radius:var(--border-TopRight-Radius-hvr);
		border-top-left-radius:var(--border-TopLeft-Radius-hvr);
		border-bottom-right-radius:var(--border-BottomRight-Radius-hvr);
		border-bottom-left-radius:var(--border-BottomLeft-Radius-hvr);
        transition:var(--transition);
	}
	transition:var(--transition);
	
}

.wp-block-vayu-blocks-advance-container .th-con{
	--gap:initial;
	gap:var(--gap);
}

.wp-block-th-blocks-container-overlay {
	--background-overlay-hvr:var(--background-overlay);
	--background-overlay-repeat-hvr: var(--background-overlay-repeat);
	--background-overlay-size-hvr: var(--background-overlay-size);
	--background-overlay-position-hvr: var(--background-overlay-position);
	--background-overlay-attachment-hvr: var(--background-overlay-attachment);
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: var(--background-overlay);
	transition:var(--transition);
	background-repeat: var(--background-overlay-repeat);
	background-size: var(--background-overlay-size);
	background-position: var(--background-overlay-position);
	background-attachment: var(--background-overlay-attachment);
	border-radius: inherit;
	
}

.wp-block-vayu-blocks-advance-container:hover .wp-block-th-blocks-container-overlay{
	background: var(--background-overlay-hvr);
	background-repeat: var(--background-overlay-repeat-hvr);
	background-size: var(--background-overlay-size-hvr);
	background-position: var(--background-overlay-position-hvr);
	background-attachment: var(--background-overlay-attachment-hvr);
	border-radius: inherit;
}

.wp-block-vayu-blocks-advance-container .th-shaper .th-shape-top svg{
	width:var(--shaper-top-width);
    height:var(--shaper-top-height);
}

.wp-block-vayu-blocks-advance-container .th-shaper .th-shape-bottom svg{
	width:var(--shaper-bottom-width);
    height:var(--shaper-bottom-height);
}

.wp-block-vayu-blocks-advance-container .th-shaper .th-shape-top{
	z-index:var(--shaper-z-index-top);
}
.wp-block-vayu-blocks-advance-container .th-shaper .th-shape-bottom{
	z-index:var(--shaper-z-index-bottom);
}

.th-root-block-container {
	position:relative
}

.wp-block-vayu-blocks-advance-container *,
.wp-block-vayu-blocks-advance-containe{
	box-sizing: border-box;
}
.th-shape{
z-index:0;
}
@media only screen and (min-width: 1024px)  {
	.wp-block-vayu-blocks-advance-container.th-hide-desktop:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

	.block-editor-block-list__layout .block-editor-block-list__block.th-hide-desktop.wp-block-vayu-blocks-advance-container{
	 position: initial;
	pointer-events:none;
	}
	
}


@media only screen and (min-width: 768px) and (max-width: 1023px) {
	.wp-block-vayu-blocks-advance-container.th-hide-tablet:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

.block-editor-block-list__layout .block-editor-block-list__block.th-hide-tablet.wp-block-vayu-blocks-advance-container{
	position: initial;
	pointer-events:none;
}

}

@media only screen and (max-width: 767px) {
  
	.wp-block-vayu-blocks-advance-container.th-hide-mobile:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}

	.block-editor-block-list__layout .block-editor-block-list__block.th-hide-mobile.wp-block-vayu-blocks-advance-container{
	 position: initial;
	pointer-events:none;
	}

}